import rubbishs from '@/assets/rubbish.json'
const results = [{
  name: '可回收',
  categroy: 1,
}, {
  name: '有害垃圾',
  categroy: 2,
}, {
  name: '厨余（湿垃圾）',
  categroy: 4,
}, {
  name: '其他（干垃圾）',
  categroy: 8,
}]
// 完成数超过66则成功
const maxCompleteCount = 1
let interval

export default {
  components: {},
  data() {
    return {
      question: {},
      progress: 0,
      // 计时器
      time: 0,
    }
  },
  computed: {
    /**
     * 用于显示的，即消除浮点问题的计时器
     */
    showTime() {
      return this.time.toFixed(1)
    },
  },
  mounted() {
    this.startTime()
    this.generateQuestion()
  },
  methods: {
    startTime() {
      interval = setInterval(() => {
        this.time += .1
      }, 100)
    },
    generateQuestion() {
      this.question = rubbishs[Math.floor(Math.random() * rubbishs.length)]
    },
    checkResult(categroy) {
      // 答对了的情况，加速弹框显示，增加一次
      if (categroy === this.question.categroy) {
        // 66次答完
        this.progress++
        if (this.progress >= maxCompleteCount) {
          clearInterval(interval)
          uni.showModal({
            title: '提示',
            content: `完成，耗时${this.showTime}秒。`,
            success: function(res) {
              if (res.confirm) {
                console.log(11111)
                uni.share({
                  type: 5,
                  scene: 'WXSceneSession',
                  title: '1',
                  summary: '2',
                  miniProgram: {
                    id: '',
                    path: '/pages/rubbish_categary/index'
                  }
                })
              } else if (res.cancel) {

              }
            }
          })
          return
        }
        this.generateQuestion()
      } else {
        let duration = 3000
        uni.showToast({
          icon: 'none',
          title: `答错，${results.find(i => i.categroy === this.question.categroy).name}`,
          mask: true,
          duration,
          success: () => {
            setTimeout(() => {
              this.generateQuestion()
            }, duration)
          }
        })
      }
    },
  }
}
